@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.prototype-container {
    justify-content: center;
}

.prototype-container img {
    width: 60%;
}

.overview-description {
    padding-top: 8.8rem;
    margin     : 0 auto;
    text-align : center;

    p:first-of-type {
        margin-top : 7.1rem;
        font-size  : 3.2rem;
        font-weight: bold;
        line-height: 4rem;

        ~p {
            font-size     : 1.6rem;
            letter-spacing: 0.03rem;
            line-height   : 2.2rem;
            padding       : 2.4rem 0;
            color         : #737B7D;
        }
    }
}

@media screen and(max-width:540px) {
    .overview-description {
        padding    : 0px;
        padding-top: px2remm(48px);
        
        p:first-of-type {
            line-height: px2remm(50px);
            font-size  : px2remm(21px) !important;

            ~p {
                font-size     : px2remm(16px);
                letter-spacing: px2remm(0.3px);
                line-height   : px2remm(22px);
                padding       : 0px px2remm(21px) 0px;
                text-align    : left;
            }
        }

    }

    .prototype-container {
        margin-top: px2remm(32px);

        img {
            width     : px2remm(302px);
            height    : auto;
            text-align: center;
            margin    : 0px;
        }

    }
}