.form-container {
    margin        : 0;
    flex-direction: column;
    align-items   : center;
    width         : 100%;

    form {
        width: auto;
    }

    .form-response {
        position        : absolute;
        width           : 55.7rem;
        background-color: rgb(253, 250, 250);
        border-radius   : 0.8rem;

        .close {
            margin-top : 5.4rem;
            margin-left: 43.7rem;
            cursor     : pointer;
        }

        .description {
            display       : flex;
            flex-direction: column;
            margin-top    : 11.8rem;
            text-align    : center;
            width         : 100%;

            p:first-of-type {
                font-size    : 3.2rem;
                line-height  : calc(40/32);
                margin-bottom: 5.6rem;

                ~p {
                    margin   : auto 4.8rem;
                    font-size: 1.8rem;
                }
            }

            img {
                width     : 30rem;
                height    : auto;
                align-self: center;
                margin-top: 5rem;
            }
        }
    }
}

.ip-group {
    margin-top: 2.4rem;
    outline   : none;
}

.ip-group input {
    margin-top  : 0.6rem;
    width       : 36rem;
    display     : block;
    border-style: none;
    padding     : 1.2rem 1.6rem;
    box-shadow  : 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
    outline     : none;
}

.ip-group textarea {
    margin-top  : 0.6rem;
    width       : 36rem;
    height      : 18rem;
    display     : block;
    border-style: none;
    padding     : 1.2rem 1.6rem;
    box-shadow  : 0 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
    resize      : none;
    outline     : none;
}

.form-container input[type="submit"] {
    margin-top      : 4.6rem;
    position        : relative;
    left            : 50%;
    transform       : translateX(-50%);
    border-style    : none;
    padding         : 1.8rem 1.8rem;
    width           : 16rem;
    background-color: #3C64B1;
    color           : #ffffff;
    cursor          : pointer;
    border-radius   : 0.8rem;
    font-size       : 2.4rem;
}

.contact {
    padding: 6.4rem 10%;
}

.contact-form {
    width : 50%;
    margin: 0 11.1rem;
}



.contact-figure {
    width       : 50%;
    margin-right: 10%;

    .contact-title {
        text-align   : center;
        margin-bottom: 2rem;

        h1 {
            font-size  : 3.2rem;
            line-height: calc(40/32);
            font-weight: bold;
        }

        p {
            font-size     : 1.6rem;
            line-height   : calc(22/16);
            letter-spacing: 0.03rem;
            color         : #737B7D;

            a {
                text-decoration: none;
            }
        }
    }

    .figure-img {
        text-align: center;

        img {
            width : 90%;
            height: auto;
        }
    }
}

.form-title {
    font-size  : 3.2rem;
    line-height: calc(40/32);
    font-weight: bold;
}

.form-note {
    margin-top: 1.4rem;
    width     : 32.7rem;
}

.g-recaptcha {
    height: 5rem;
}

.rc-anchor {
    width: 56rem !important;


}

.contact-container {
    margin-top     : 10rem;
    justify-content: center;
    align-items    : center;
    flex-wrap      : nowrap;
}


@media only screen and (min-width:540px) and (max-width:860px) {
    .contact-figure {
        display: none;
    }
}

@media screen and(max-width:540px) {
    #contact-us{
        display: none;
    }
}