@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

@mixin player-button {
    display        : inline-block;
    margin-left    : 3.2rem;
    height         : 6rem;
    line-height    : calc(60 / 18);
    font-size      : 1.8rem;
    color          : #374ab8;
    letter-spacing : 0.03rem;
    text-decoration: none;

    img {
        line-height   : calc(60 / 18);
        width         : 2.4rem;
        height        : auto;
        vertical-align: middle;
        margin-right  : 1.5rem;
    }
}

.video-container {
    position: fixed;
    width   : 80vw;
    height  : 70vh;
    z-index : 130;
}

.react-player {
    position: absolute;
    top     : 0;
    left    : 0;
}

.how-it-works {
    display        : flex;
    padding-top    : 8.8rem;
    justify-content: space-around;
    align-items    : center;
    width          : 100%;
    flex-wrap      : nowrap;

    div:first-of-type {
        margin-top : 1rem;
        width      : auto;
        flex-shrink: 1;
        width      : 38.3%;

        img {
            width : 100%;
            height: auto;
        }

        &+div {
            width        : 50%;
            height       : auto;
            // margin-top: 14rem;

            p:first-of-type {
                font-size     : 4.4rem;
                font-style    : normal;
                font-weight   : bold;
                line-height   : calc(56 / 44);
                letter-spacing: 0.02rem;
                color         : #373f41;

                &+p {
                    margin-top    : 2rem;
                    font-size     : 1.6rem;
                    font-style    : normal;
                    font-weight   : normal;
                    line-height   : calc(22 / 16);
                    letter-spacing: 0.03rem;
                    color         : #737b7d;

                    &+div {
                        width      : 100%;
                        display    : flex;
                        margin-left: 0;
                        margin-top : 3.5rem;

                        button {
                            a {
                                font-weight     : bold;
                                font-size       : 2.4rem;
                                background-color: #3c64b1;
                                color           : white;
                                border-radius   : 0.8rem;
                                padding         : 1.8rem 4rem;
                                cursor          : pointer;
                                text-decoration : none;
                            }
                        }

                        .youtube {
                            float: left;
                            cursor: pointer;
                            @include player-button;
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (min-width:540px) and (max-width:860px) {
    .how-it-works {
        flex-direction: column-reverse;
        flex-wrap     : wrap;
        width         : px2remm(320px);
        width         : 100%;

        div:first-of-type {
            text-align: center;

            img:first-of-type {
                width       : 80%;
                margin-right: 0px;
            }

            ~div {
                margin-top: px2remm(30px) !important;
                height    : auto;
                padding   : 0 px2remm(30px);
                width     : 100%;

                p:first-of-type {
                    font-size  : px2remm(33px);
                    line-height: px2remm(50px);

                    &+p {
                        +div {
                            display        : flex;
                            justify-content: center;
                            align-items    : center;

                            button {
                                a {
                                    width        : px2remm(130px);
                                    height       : px2remm(39px);
                                    padding      : 1rem;
                                    border-radius: px2remm(5px);
                                    font-size    : px2remm(15px);
                                }
                            }

                            .youtube {
                                font-size     : px2remm(15px);
                                color         : #374ab8;
                                letter-spacing: px2remm(0.1px);
                                height        : auto;
                                cursor: pointer;

                                img {
                                    line-height: px2remm(39px);
                                    width      : px2remm(18px);
                                    height     : px2remm(18px);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

@media only screen and (max-width:540px) {
    .how-it-works {
        flex-direction: column-reverse;

        div:first-of-type {
            text-align: center;
            width     : 100%;

            img:first-of-type {
                width : 87.5%;
                margin: 0px;
            }

            ~div {
                margin-top: px2remm(30px) !important;
                padding   : 0 px2remm(24px);
                width     : 100%;

                p:first-of-type {
                    font-size  : px2remm(33px);
                    line-height: px2remm(50px);

                    &+p {
                        +div {
                            display        : flex;
                            justify-content: space-around;
                            align-items    : center;

                            button {
                                a {
                                    width        : px2remm(130px);
                                    height       : px2remm(39px);
                                    padding      : 0;
                                    border-radius: px2remm(5px);
                                    font-size    : px2remm(15px);
                                    padding      : 1rem 2rem;

                                }
                            }

                            .youtube {
                                font-size     : px2remm(15px);
                                color         : #374ab8;
                                letter-spacing: px2remm(0.1px);
                                height        : auto;
                                margin        : 0px;

                                img {
                                    line-height: px2remm(39px);
                                    width      : px2remm(18px);
                                    height     : px2remm(18px);
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}