@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.footer {
    height         : auto;
    width          : 100%;
    display        : flex;
    margin-top     : 10rem;
    font-size      : 1.4rem;
    justify-content: space-around;
    flex-wrap      : wrap;
    padding: 0 1rem;

    img {
        height: 20rem;
        width : auto;
    }

    div:nth-child(4) {
        margin-left: 5rem;
    }

    div:first-of-type {
        margin-top: 5.6rem;

        &+div {
            font-size  : 1.6rem;
            margin     : 9.6rem 2.5rem 3rem 1.2rem;
            flex       : 0 1 57rem;
            flex-grow  : 1;
            flex-shrink: 1;
            flex-basis : 35rem;

            p:first-child {
                font-style : normal;
                font-weight: bold;
                font-size  : 3rem;
                line-height: calc(42/30);
                color      : #FC8A21;

                &+p {
                    font-size  : 1.4rem;
                    line-height: calc(20/14);
                    color      : #000000;
                    width      : unset;
                }
            }

            &+div,
            &+div+div {
                margin-top   : 9.6rem;
                // flex-basis: 10rem;

                p {
                    margin-bottom: 3rem;
                    font-weight  : bold;
                    cursor       : pointer;

                    a {
                        color          : black;
                        font-size      : 1.4rem;
                        text-decoration: none;
                    }

                }

                ul {
                    list-style: none;
                    margin    : 0;
                    padding   : 0;

                    li {
                        margin-bottom: 1.6rem;

                        a:link,
                        a:visited,
                        a:hover,
                        a:active {
                            text-decoration: none;
                            color          : black;
                            font-style     : normal;
                            font-weight    : bold;
                            font-weight    : normal;
                            line-height    : calc(20/14);
                        }

                        a {
                            padding-right: 1.6rem;
                            cursor       : pointer;

                            img {
                                display: inline-block;
                                width  : 2rem;
                                height : auto;
                            }
                        }
                    }

                }
            }
        }
    }
}

.copyright {
    font-size  : 1.4rem;
    font-style : normal;
    font-weight: normal;
    line-height: calc(20/14);
    text-align : left !important;
    margin-left: 13.7rem;
    margin     : 5rem 0 3rem 13.7rem;
    text-align : left;
}

/* mobile style */
@media screen and(max-width:540px) {
    #footer {
        .footer {
            display              : grid;
            grid-template-columns: repeat(2, auto-fill);
            grid-template-rows   : repeat(2, auto-fill);
            height               : auto;
            padding-bottom       : px2remm(40px);
            background           : rgba(60, 100, 177, 0.06);

            div:first-of-type {
                display         : none;
                background-color: red;
            }

            .description-mobile {
                // background-color: red;
                grid-row   : 1/2;
                grid-column: 1/3;
                float      : none;
                margin     : px2remm(40px) px2remm(32px);

                p:first-child {
                    font-size  : px2remm(30px);
                    line-height: px2remm(42px);

                    &+p {
                        width      : auto !important;
                        font-size  : px2remm(14px);
                        line-height: px2remm(20px);
                        margin     : px2remm(13px) 0px 0px;
                        height     : auto;
                    }
                }
            }

            .nav-item,
            .nav-item~div {
                margin      : 0px px2remm(32px);
                width       : auto;
                margin-right: 0px;

                p {
                    padding-bottom: px2remm(14px);

                    a {
                        font-size  : px2remm(14px);
                        line-height: 1.4;
                    }
                }

                ul {
                    li {
                        margin-top : px2remm(16px);
                        font-size  : px2remm(14px);
                        line-height: 20px;

                        img {
                            width       : px2remm(18px);
                            height      : px2remm(18px);
                            margin-right: px2remm(7px);
                        }

                        a {
                            font-size  : px2remm(14px);
                            line-height: 20px;
                            padding-right: 1rem !important;
                        }
                    }

                }
            }
        }
    }

    .copyright {
        display: none;
    }
}


@media only screen and (min-width:540px) and (max-width:860px) {
    .footer {
        width: 100%;

        .nav-item {
            width     : 30%;
            margin-top: 0px !important;

            ~div {
                margin-top: 0px !important;
            }
        }
    }
}