@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.user-flow-container {
    padding       : 0 8.6rem;
    flex-direction: column;
}

.user-flow-top {
    justify-content: space-between;
}

.uf-card-container {
    margin: 0;

    &.card {
        position: relative;
        height  : 57.4rem;
    }
}

.uf-figure {
    height             : 30rem;
    margin             : 2.4rem 2rem;
    background-size    : contain;
    background-repeat  : no-repeat;
    background-position: center;
}

.uf-title {
    font-weight: bold;
    font-size  : 1.8rem;
}

.uf-card-container .uf-title,
.uf-card-container .description {
    text-align: center;
}

.uf-card-container {
    .description {
        margin     : 6.4rem auto;
        font-size  : 1.8rem;
        line-height: 2.6rem;
        padding    : 0px 1rem;
    }

    &.card {
        width: 30%;
    }
}

.get-start-container {
    display        : flex;
    flex-direction : row;
    flex-wrap      : wrap;
    margin         : 4.6rem 0;
    justify-content: center;
    align-items    : center;
}

.get-start-container .txt-title {
    font-size: 3.2rem;
    margin   : 0.4rem 0;
}

.get-start-container .btn-body {
    background-color: #3C64B1;
    font-weight     : bold;
    text-align      : center;
    width           : 20rem;
    margin-right    : 8.8rem;
    border-radius   : 0.8rem;
    margin-bottom   : 1rem;

    a {
        color          : #ffffff;
        line-height    : 7rem;
        font-size      : 2.4rem;
        text-decoration: none;
        padding        : 1.8rem 2.4rem;


    }
}

.get-start-txt {
    padding    : 5rem 0;
    margin-left: 6rem;

    .description {
        width    : 75rem;
        font-size: 1.8rem;

    }
}

@media only screen and (min-width:540px) and (max-width:860px) {
    .user-flow-bottom {
        .get-start-container {
            display       : flex;
            flex-direction: column;
            align-items   : center;

            .get-start-txt {
                margin-left: 0px !important;

                .description {
                    width: 100%;
                }
            }

        }

    }
}

@media screen and(max-width:540px) {
    .getting-start {
        .section-header {
            font-size  : px2remm(21px);
            line-height: px2remm(50px);
            margin-top : px2remm(38px);
        }
    }

    .user-flow-container {
        &.flexbox {
            margin : 0px;
            padding: 0px;
        }

        .user-flow-top {
            display       : flex;
            flex-direction: column;
            align-items   : center;
            padding       : 0px;
            margin        : 0px;

            .uf-card-container {
                box-shadow   : 0px px2rem(14px) px2rem(14px) rgba(0, 0, 0, 0.25) !important;
                margin-bottom: px2remm(27px) !important;

                &.card {
                    width        : px2remm(240px);
                    border-radius: px2rem(8px);
                    height       : auto;
                    margin       : 0 px2remm(40px);

                    .uf-figure {
                        width     : px2remm(200px);
                        min-height: px2remm(180px);
                        margin    : px2remm(22px) px2remm(24px) px2remm(14px) px2remm(24px);
                    }

                    .uf-title {
                        font-size     : px2remm(18px);
                        line-height   : px2remm(24px);
                        letter-spacing: px2remm(0.1px);
                    }

                    .description {
                        padding    : 0 px2remm(16px);
                        margin     : 0px 1rem;
                        font-size  : px2remm(18px);
                        line-height: px2remm(26px);
                    }
                }
            }
        }

        .user-flow-bottom {
            .get-start-container {
                display       : flex;
                flex-direction: column;
                padding       : 0;
                margin        : 0;
                height        : auto;
                box-shadow    : none;

                .get-start-txt {
                    width  : 100%;
                    padding: 0;
                    margin : 0;

                    .txt-title {
                        font-size  : px2remm(21px);
                        line-height: px2remm(50px);
                        font-weight: bold;
                        text-align : center;
                    }

                    .description {
                        width      : auto;
                        font-size  : px2remm(14px);
                        line-height: px2remm(20px);
                        margin     : 0 px2remm(24px);
                    }
                }

                .get-start-btn {
                    margin: px2remm(18px);

                    .btn-body {
                        width        : px2remm(146px);
                        height       : px2remm(44px);
                        border-radius: px2remm(5px);
                        display      : inline-block;
                        margin       : 0 auto;

                        a {
                            display        : inline-block;
                            color          : #ffffff;
                            text-decoration: none;
                            padding        : 0;
                            margin         : 0;
                            line-height    : px2remm(44px);
                            font-size      : px2remm(15px);
                        }
                    }
                }
            }
        }
    }
}