.team-title {
    text-align: center;
    margin-top: 13.2rem;

    h1 {
        font-size  : 3.2rem;
        line-height: calc(40/32);
        font-weight: bold;
    }
}

.slider-container {
    .swiper-pagination-bullets {
        text-align   : right;
        padding-right: 10rem;
    }

    .swiper-button-next,
    .swiper-button-pre {
        color: gray;
    }

    margin : 0 auto;

    .mb-info {
        width: 50rem;
    }

    .mb-words {
        text-align    : center;
        padding-bottom: 2.4rem;
        font-size     : 1.6rem;
        line-height   : 2.2rem;
    }

    .flexbox {
        flex-wrap      : nowrap;
        justify-content: space-evenly;

        .mb-image {
            flex-shrink: 0 1 auto;
            width      : 31%;

            img {
                width      : 100%;
                height     : auto;
                margin-left: 10rem;
            }
        }

        .mb-info {
            row-gap        : 10rem;
            width          : 31%;
            display        : flex;
            flex-direction : column;
            justify-content: center;

            .mb-title {
                font-size: 2.6rem;
            }

            .mb-name {
                font-size: 3.2rem;

                a img {
                    width        : 2.5rem;
                    border-radius: 0.2rem;
                }
            }

            .mb-intro {
                margin-top : 2.5rem;
                font-weight: 300;
                font-size  : 2.2rem;
                line-height: 2.8rem;
            }
        }
    }
}

@media only screen and (min-width:540px) and (max-width:860px) {

    #team {

        .slider-container {
            .swiper-pagination-bullets {

                padding-right: 0rem;
            }

            .mb-info {
                width        : 50rem;
                padding-right: 0rem;

            }

            .flexbox {
                align-items: center;

                .mb-image {
                    width     : 50%;
                    text-align: center;

                    img {
                        width      : 80%;
                        margin-left: 0rem;
                    }
                }

                .mb-info {
                    width       : 50%;
                    margin-right: 4rem;
                }
            }
        }
    }
}

@media only screen and (max-width:540px) {
    #team {
        display: none;
    }
}