@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.section-header {
    font-size  : 3.2rem;
    font-weight: normal;
    text-align : center;
    margin     : 6.4rem 0 0px;
    font-family: Muli;
    line-height: px2rem(40/32);
    font-weight: bold;
}

.getting-start h1 {
    padding-bottom: 3rem;
}

@media screen and(max-width:540px) {
    .home-page {
        margin-top:px2remm(10px);
    }
}