.question-container{
    margin: 2rem 0;
    padding: 3.6rem 3.6rem 3.6rem 3.6rem; 
    background-color: rgba(60, 101, 177, 0.06);
    transition-delay: 1s;
    transition: .5s ease-in-out;
}


.question-container:hover .question-answer{
    opacity: 1;
    margin-top: 1.2rem;
    transition: .5s ease-in-out;
}

.question-container .question{
    font-size: 2rem;
}

.question-container .question-answer{
    opacity: 0;
    font-size: 1.8rem;
    margin-top: -2.4rem;
    transition: .5s ease-in-out;
}

.question-list-container{
    padding: 6.4rem 12rem;
    padding-bottom: 4.8rem;
    ~p{
        text-align: left;
        margin-left: 15.6rem;
        font-size: 2rem;
        line-height: calc(24/20);
    }
}