@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.homepage-feature {
    margin        : 3.6rem 0;
    flex-wrap     : nowrap;
    flex-direction: row;
    width         : auto;
    justify-content: center;
}

.feature-list-container {
    justify-content: center;
    align-items: center;
    flex-wrap      : nowrap;
    margin-top     : 8.3rem;
    padding        : 0;
    width          : 100%;


    .list-content {
        width: 50%;
        .section-title {
            text-align: center;

            p {
                padding-top   : 2.4rem;
                font-size     : 1.6rem;
                letter-spacing: 0.03rem;
                color         : #737B7D;
            }
        }

        .homepage-feature-list {
            .homepage-feature {
                .homepage-ft-icon {
                    align-self: center;

                    img {
                        width : 4.2rem;
                        height: auto;
                    }
                }

                .homepage-ft-txt {
                    margin-left: 2.4rem;
                    width      : 44.6rem;

                    .txt-title {
                        font-size: 2.6rem;
                    }

                    .txt-description {
                        font-size  : 1.8rem;
                        color      : #737B7D;
                        margin-top : 1.2rem;
                        line-height: 2.6rem;
                    }
                }

            }
        }
    }

    .list-background {
        width: 50%;
        img {
            width : 100%;
            height: auto;
        }
    }
}

@media screen and(max-width:540px) {
    .feature-list-container {
        width         : 100%;
        margin        : 0;
        padding       : 0;
        flex-direction: column;

        .list-content {
            width: 100%;

            .section-title {
                padding: 0px 1rem;
                margin-top: 13.2rem;
                h1{
                    font-size: 2.1rem;
                    line-height: 5rem;
                    margin: 0px;
                }
                p{
                    padding-top: 0px;
                }
            }
        }



        .list-background {
            width  : 100%;
            padding: 0px 2rem;

            img {
                display: none;
            }
        }

        .homepage-feature-list {
            .homepage-feature {
                flex-wrap: nowrap;
                padding  : 0px 1rem;

                .homepage-ft-icon {
                    img {
                        height: 4.2rem;
                        width : auto;
                    }
                }

                .homepage-ft-txt {
                    margin-left: 1rem;

                    .txt-title {
                        font-size  : 1.8rem !important;
                        line-height: 2.3rem !important;
                    }

                    .txt-description {
                        font-size  : 1.4rem !important;
                        line-height: 2rem !important;
                    }
                }
            }
        }
    }
}

@media only screen and (min-width:540px) and (max-width:860px) {
    .feature-list-container {
        width         : 100%;
        flex-direction: column;
        .list-content {
            width: 100%;

            .section-title {
                padding: 0px 1rem;
            }
        }


        .list-background {
            width  : 100%;
            padding: 0px 2rem;
            text-align: center;

            img {
                width: 80%;
            }
        }

        .homepage-feature-list {
            .homepage-feature {
                justify-content: center;
            }
        }
    }
}