@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}
@media screen and(max-width:800px) {
    div {
        .question-list-container-mobile {
            margin-top: px2remm(133px);
            h1{
                font-size: px2remm(21px);
                line-height: px2remm(50px);
            }
            .question-container-mobile{
               margin: px2remm(20px) px2remm(24px); 
               background: rgba(60, 100, 177, 0.06);
               border-radius: px2remm(10px);
               padding: px2remm(20px) px2remm(10px) px2remm(15px);
               .question{
                   font-size: px2remm(16px);  
                   line-height: px2remm(20px); 
                   margin-bottom: px2remm(15px);                
               }
               .question-answer{
                   font-size: px2remm(14px);
               line-height: px2remm(20px);
               }
            }
        }
        .get-start-txt {
            width  : 100%;
            padding: 0;
            margin : 0;

            .txt-title {
                font-size  : px2remm(21px);
                line-height: px2remm(50px);
                font-weight: bold;
                text-align : center;
            }

            .description {
                width      : auto;
                font-size  : px2remm(14px);
                line-height: px2remm(20px);
                margin     : 0 px2remm(0px);
                margin-bottom: px2remm(18px);
                padding: 0  px2remm(15px);
            }
        }
        .get-start-btn {
            // background-color: red;
            text-align: center;
            .btn-body {
                width        : px2remm(146px);
                height       : px2remm(44px);
                border-radius: px2remm(5px);
                display      : inline-block;
                margin       : 0 auto;
                margin-bottom: px2remm(40px);
                background: #3C64B1;
                text-align: center;

                a {
                    display        : inline-block;
                    color          : #ffffff;
                    text-decoration: none;
                    padding        : 0;
                    margin         : 0;
                    line-height    : px2remm(44px);
                    font-size      : px2remm(15px);
                }
            }
        }
    }
}