@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.section-container {
    .section-title {
        text-align: center;
        margin-top: 13.2rem;

        h1 {
            font-weight: bold;
            font-size  : 3.2rem;
            line-height: 4.0rem;
        }

        p {
            font-size  : 1.6rem;
            line-height: 2.2rem;
        }
    }
}

.demo-container {
    margin-top     : 2rem;
    justify-content: center;
}

.demo-tabs {
    margin-right  : 4rem;
    flex-direction: column;
    align-items   : left;
}

.tab-btn-container {
    padding      : 2.5rem 0 1.6rem;
    align-items  : center;
    border-bottom: 0.3rem solid #ffffff00;
}

.tab-btn-container:hover {
    background-color: rgba(180, 201, 250, 0.2);
    border-bottom   : 0.3rem solid #5f81e9;
    cursor          : pointer;
}

.tab-btn-container .tab-btn-icon img {
    width : 4.2rem;
    height: 4.2rem;
    margin: 0 2rem;
}

.demo-btn {
    margin-top   : 2.4rem;
    height       : 6rem;
    align-self   : center;
    background   : #3C64B1;
    border-radius: 0.5rem;
    text-align   : center;

    a {
        text-decoration: none;
        font-size      : 2.4rem;
        color          : white;
        line-height    : 6rem;
        padding        : 1.8rem 2.4rem;
        font-weight    : bold;
        color          : white;
    }
}

.demo-view {
    background-color: rgba(180, 201, 250, 0.2);
    width           : 83.5rem;
    height          : auto;
}

.tab-btn-txt .title {
    font-size  : 1.8rem;
    line-height: calc(24/18);
    font-weight: bold;

    ~div {
        padding-top   : 1.5rem;
        font-size     : 1.4rem;
        letter-spacing: 0.02rem;
        line-height   : calc(20/14);
        color         : #373F41;
        width         : 24rem;
    }
}

.keywords-container {
    margin-top      : 6.4rem;
    padding         : 7.8rem 10rem;
    justify-content : space-around;
    background-color: rgba(180, 201, 250, 0.2);
}

.keyword-txt {
    margin-top    : 2.4rem;
    font-size     : 1.8rem;
    font-weight   : bold;
    line-height   : calc(24/18);
    letter-spacing: 0.01rem;
    color         : #373F41;
}

.keyword-item {
    flex-direction : column;
    align-items    : center;
    justify-content: space-between;
}

.keyword-item img {
    width          : 4rem;
    height         : auto;
    object-fit     : contain;
    object-position: center;
}

@media only screen and(max-width:540px) {
    .section-container {
        .section-title {
            margin-top: 13.2rem;
    
            h1 {
                font-weight: bold;
                font-size  : 2.1rem;
                line-height: 5rem;
            }
    
            p {
                font-size  : 1.6rem;
                line-height: 2.2rem;
            }
        }
    }
    .demo-container {
        margin-top: 1rem;
        width     : 100%;

        .demo-tabs {
            width          : 90%;
            justify-content: center;
            margin         : 0px;

            .tab-btn-container {
                padding           : 1rem;
                flex-wrap         : nowrap;

                .tab-btn-icon {
                    img {
                        margin: 0px;
                    }
                }

                .tab-btn-txt {
                    margin-left: 1rem;
                }
            }

            .tab-btn-txt {
                .description {
                    width: auto;
                }
            }
        }

        .demo-view {
            display: none;
        }
    }

    .keywords-container {
        .keyword-item {
            display: none;
        }

        display: none;
        padding: 0px;
        margin : 0px;
    }
}

@media only screen and (min-width:540px) and (max-width:860px) {
    .demo-container {
        margin-top: 1rem;
        width     : 100%;

        .demo-tabs {
            width          : 100%;
            justify-content: center;
            margin         : 0px;

            .tab-btn-container {
                padding           : 2rem;
                // justify-content: center;
                flex-wrap         : nowrap;
            }

            .tab-btn-txt {
                .description {
                    width: auto;
                }
            }
        }

        .demo-view {
            display: none;
        }
    }

    .keywords-container {
        padding: 1rem 2rem;
    }
}