@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

header {
    .mobile-nav {
        display: none;
    }

    font-size       : 1.4rem;
    height          : 8.8rem;
    background-color: #f3f6fa;
    width           : 100%;
    position        : fixed;
    z-index         : 100;

    .flexbox {
        flex-wrap      : nowrap;
        justify-content: space-around;
    }

    .logo {
        display     : flex;
        margin-top  : 0.7rem;
        width       : auto;
        flex-shrink : 1;
        margin-right: 0rem;

        img,
        div {
            height     : 7.5rem;
            width      : 7.5rem;
            cursor     : pointer;
            flex-shrink: 1;
        }

        div {
            width: auto;

            a {
                line-height    : 7.5rem;
                font-weight    : bold;
                font-size      : 2.4rem;
                color          : #FC8A21;
                letter-spacing : 0.03rem;
                margin-left    : 2rem;
                text-decoration: none;
            }
        }
    }

    .nav-open {
        display: none;
    }

    nav {
        height     : 8.8rem;
        font-size  : 1.4rem;
        font-style : bold;
        color      : #3C64B1;
        line-height: calc(18/14);
        display    : flex;
        align-items: center;
        visibility : visible;


        .nav-icon {
            display: none;
        }

        li {
            list-style: none;
            display   : inline-block;
            cursor    : pointer;
        }

        .web-nav {
            display        : flex;
            flex-shrink    : 1;
            justify-content: space-between;

            margin: 3.5rem 0;

            li a {
                margin-right   : 2.5rem;
                font-size      : 1.4rem;
                font-style     : normal;
                font-weight    : 600;
                color          : black;
                text-decoration: none;

                &:hover {
                    color: #3C64B1;
                }
            }

            li:last-of-type {
                margin-left: 4rem;

                a {
                    color: #3C64B1;
                }
            }
        }

        ul:first-of-type~a {
            font-size       : 1.4rem;
            font-weight     : bold;
            line-height     : calc(18/14);
            font-style      : normal;
            padding         : 1.2rem 1.8rem;
            color           : white;
            background-color: #3C64B1;
            cursor          : pointer;
            border-radius   : 0.5rem;
            letter-spacing  : 0.03rem;
            height          : 4.2rem;
            text-decoration : none;
        }
    }
}

@media only screen and (min-width:540px) and (max-width:860px) {
    header {
        .header-container {
            nav {
                border-radius: 1rem;
                z-index      : 10;

                .web-nav {

                    :nth-child(3),
                    :nth-child(4),
                    :nth-child(5) {
                        display: none;
                    }

                    li a {
                        margin-right: 1.5rem;
                        font-size   : 1.4rem;
                    }

                    li:last-of-type {
                        margin-left: 1rem;
                    }
                }

                .nav-icon {
                    visibility: hidden;
                }
            }

            .logo {
                div {
                    a {
                        margin-left: 1rem;

                    }
                }
            }
        }
    }
}

@media only screen and (max-width:540px) {
    header {
        *{
            padding:0px;
            margin:0px;
        }
        .web-nav {
            display: none !important;
        }

        .mobile-nav {
            display: block !important;
        }

        .header-container {
            .nav-open {
                display: block;
            }

            nav {
                height          : auto;
                margin-top      : 3.6rem;
                background-color: white;
                border-radius   : 1rem;
                z-index         : 10;
                position        : absolute;
                width           : 80%;
                flex-direction  : column;
                visibility      : hidden;

                #mobile-nav {
                    background-color: rgb(255, 255, 255);

                    display   : block;
                    text-align: center;
                    width     : 90%;
                    margin    : 0rem 5%;
                    padding   : 0rem 2rem;

                    li:first-of-type {
                        text-align   : right;
                        padding-right: 2rem;
                        border-radius: none;
                        background-color: #ffffff;

                    }

                    li {
                        display         : block;
                        margin          : auto 0px;
                        margin-bottom   : px2remm(28px);
                        background-color: #E9EFF5;
                        border-radius   : px2remm(5px);

                        padding: px2remm(7px) 0px;

                        a {
                            font-size      : px2remm(14px);
                            line-height    : px2remm(18px);
                            text-decoration: none;
                        }
                        a:link,a:visited,a:hover,a:active{
                            color: black;
                        }
                    }
                }

                .nav-icon {
                    display      : block;
                    position     : relative;
                    margin-top   : px2remm(34px);
                    margin-bottom: px2remm(45px);
                    margin-right : 4.5rem;
                    text-align   : right;
                    align-self   : flex-end;

                    img {
                        display: inline-block;
                        cursor : pointer;
                    }
                }

                .sign-up {
                    display: none;
                }
            }
        }
    }
}