    @function px2remm($px) {
        $rem: 10px;
        @return ($px/$rem)+rem
    }

    .service-container {
        .service-title {
            text-align   : center;
            margin-top   : 13.2rem;
            margin-bottom: 9.6rem;

            h1 {
                font-size  : 3.2rem;
                line-height: calc(40/32);
                font-weight: bold;
            }

            p {
                font-size     : 1.6rem;
                line-height   : calc(22/16);
                letter-spacing: 0.03rem;
                color         : #737B7D;
            }
        }

        .service-list {
            padding              : 0px 5%;
            display              : grid;
            grid-template-columns: repeat(5, 1fr);
            grid-template-rows   : repeat(2, 1fr);
            row-gap              : 6.2rem;
            column-gap           : 4.2rem;
            align-content        : space-between;
        }

        .list-r {
            grid-column: 4 / span 2;
            grid-row   : 1 / span 2;

            .card-info {
                padding       : 0px 5.3rem;
                font-size     : 1.8rem;
                line-height   : calc(22/18);
                letter-spacing: 0.03rem;
            }

            .card-name {
                color      : #3C64B1;
                font-weight: 900;
                font-size  : 2.4rem;
                line-height: 1;
            }
        }

        .card {
            flex-direction : column;
            justify-content: center;
            align-items    : center;
            margin         : 3rem 1rem;
            box-shadow     : 0px 0.4rem 0.4rem rgba(0, 0, 0, 0.25);
            border-radius  : 0.8rem;
            text-align     : center;

        }

        .card .icon-container {
            height: auto img {
                width : 10rem;
                height: auto;
            }
        }

        .card .card-info {
            margin: 6rem 0;
        }

        .card .card-name {
            margin-top    : 3.6rem;
            font-size     : 1.8rem;
            font-weight   : bold;
            line-height   : calc(24/18);
            letter-spacing: 0.01rem;
        }

        .card .card-btn {
            padding      : 1.8rem 2.4rem;
            margin-bottom: 1rem;

            background-color: #3C64B1;
            color           : white;

            a {
                font-weight    : bold;
                font-size      : 2.4rem;
                color          : white;
                text-decoration: none;
            }

            border-radius : 0.8rem;
        }
    }

    @media only screen and (max-width:860px) {
        .service-container {
            display       : flex;
            flex-direction: column;
            height        : auto;

            .service-title {
                display      : block;
                margin-bottom: px2remm(29px);

                h1 {
                    font-size     : px2remm(21px);
                    line-height   : px2remm(50px);
                    letter-spacing: px2remm(0.1px);
                }

                p {
                    font-size     : px2remm(16px);
                    line-height   : px2remm(22px);
                    letter-spacing: px2remm(0.3px);
                    padding       : px2remm(0px) px2remm(24px);
                    text-align    : center;

                }
            }

            .service-list {
                display              : grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows   : repeat(3, 1fr) auto;
                text-align           : center;
                justify-items        : center;
                margin               : 0px;


                .card {
                    box-shadow   : 0px px2remm(4px) px2remm(4px) rgba(0, 0, 0, 0.25);
                    width        : 60%;
                    border-radius: px2remm(8px);
                    display      : block;

                    .icon-container {
                        img {
                            width     : px2remm(60px);
                            height    : auto;
                            margin-top: px2remm(31px);
                        }
                    }

                    .card-name {
                        width: 100%;

                        p {
                            font-size    : px2remm(18px);
                            line-height  : px2remm(24px);
                            margin-bottom: px2remm(36px);
                            padding      : 0 px2remm(8px);
                        }
                    }
                }

                .card-5 {
                    p {
                        word-wrap: break-word;
                    }
                }

                .list-r {
                    grid-row   : 4/5;
                    grid-column: 1/3;
                    .card {
                        width     : 60;
                        margin    : 0 20%;
                        text-align: center;

                        .card-info {
                            text-align : left;
                            padding    : px2remm(0px) px2remm(24px);
                            font-size  : px2remm(14px);
                            line-height: px2remm(20px);
                        }

                        .card-btn {
                            display: inline-block;
                            padding: 0px;

                            margin-bottom: px2remm(35px);
                            width        : px2remm(145px);
                            height       : 4.4rem;
                            a {
                                font-size     : px2remm(15px);
                                line-height: px2remm(44px);
                                height        : 4.4rem;
                            }
                        }
                    }

                }
            }
        }
    }

    @media screen and(max-width:540px) {
        .service-container {
            display       : flex;
            flex-direction: column;
            height        : auto;

            .service-title {
                display      : block;
                margin-bottom: px2remm(29px);

                h1 {
                    font-size     : px2remm(21px);
                    line-height   : px2remm(50px);
                    letter-spacing: px2remm(0.1px);
                    margin: px2remm(0px);
                }

                p {
                    font-size     : px2remm(16px);
                    line-height   : px2remm(22px);
                    letter-spacing: px2remm(0.3px);
                    padding       : px2remm(0px) px2remm(24px);
                    text-align    : left;
                }
            }

            .service-list {
                display              : grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows   : repeat(3, 1fr) auto;
                column-gap           : px2remm(18px);
                row-gap              : px2remm(24px);
                text-align           : center;
                position             : relative;
                height               : auto;

                .card {
                    box-shadow   : 0px px2remm(4px) px2remm(4px) rgba(0, 0, 0, 0.25);
                    width        : px2remm(120px);
                    border-radius: px2remm(8px);
                    display      : block;
                    height       : auto;

                    .icon-container {
                        img {
                            width     : px2remm(60px);
                            height    : px2remm(60px);
                            margin-top: px2remm(31px);
                        }
                    }

                    .card-name {
                        width: 100%;

                        p {
                            font-size    : px2remm(18px);
                            line-height  : px2remm(24px);
                            margin-bottom: px2remm(36px);
                            padding      : 0 px2remm(8px);
                        }
                    }
                }

                .card-5 {
                    p {
                        word-wrap: break-word;

                    }
                }

                .list-r {
                    grid-row   : 4/5;
                    grid-column: 1/3;
                    width      : 100%;
                    height     : 0;
                    height     : auto;

                    .card {
                        box-shadow: 0px px2remm(4px) px2remm(4px) rgba(0, 0, 0, 0.25);
                        width     : px2remm(251px);
                        margin    : 0 auto;
                        text-align: center;

                        .card-info {
                            text-align : left;
                            padding    : px2remm(0px) px2remm(24px);
                            font-size  : px2remm(14px);
                            line-height: px2remm(20px);
                        }

                        .card-btn {
                            display         : inline-block;
                            margin-bottom   : px2remm(35px);
                            width           : px2remm(145px);
                            height          : auto;
                            background-color: #3C64B1;
                            border-radius   : px2remm(5px) !important;
                            color           : white;

                            a {
                                font-weight    : normal;
                                font-size      : px2remm(15px);
                                line-height    : px2remm(44px);
                                color          : white;
                                text-decoration: none;
                                padding        : 0 !important;
                            }

                            border-radius : px2rem(8px);
                        }
                    }

                }
            }
        }

    }