@function px2remm($px) {
    $rem: 10px;
    @return ($px/$rem)+rem
}

.product-feature-list {
    background-color     : #E7EFFF;
    padding              : 6.2rem 0px;
    display              : grid;
    grid-template-rows   : 1fr 1fr;
    grid-template-columns: repeat(3, 30%);
    row-gap              : 8rem;
    // column-gap           : 8rem;
    justify-content: space-evenly;

    .product-feature {
        align-items: flex-start;

    }

    .product-feature-txt {
        margin-left: 1.4rem;
    }

    .product-feature-txt .title {
        font-weight  : bold;
        font-size    : 2rem;
        margin-bottom: 1rem;
    }

    .product-feature-txt .description {
        font-weight: regular;
        font-size  : 0.6rem;
    }

    .description {
        width      : auto;
        line-height: calc(25/16);
    }
}

@media screen and(max-width:800px) {
    .product-feature-list {
        display       : flex;
        flex-direction: column;
        padding       : px2remm(42px) 0px px2remm(33px);

        .product-feature {
            .product-feature-txt {
                padding: px2remm(0px) px2remm(24px);

                .title {
                    font-size    : px2remm(18px);
                    line-height  : px2remm(23px);
                    margin-bottom: px2remm(10px);
                }

                .description {
                    font-size    : px2remm(14px);
                    line-height  : px2remm(20px);
                    margin-bottom: px2remm(27px);
                }
            }
        }

        .description {
            width      : auto;
            line-height: calc(25/16);
        }
    }

}