@function px2rem($px) {
  $rem: 14.4px;
  @return ($px/$rem)+rem
}
@font-face
{
font-family: Muli;
src: url('./static/muli/Muli-Regular.ttf');
}
.clearfix {
  zoom: 1;
}

html{
  font-size: 62.5%;
}

* {
  margin    : 0;
  padding   : 0;
  border: 0px;
  box-sizing: border-box;
  font-size: px2rem(14px);
  font-family: Muli;
}

.clearfix:after {
  visibility: hidden;
  display   : block;
  font-size : 0;
  content   : " ";
  clear     : both;
  height    : 0;
  text-decoration: overline;
}

.flexbox{
  display: flex;
}
.web-container{
  display: flex;
  flex-direction: column;
}



